<script>
import { getListHandle } from '@/api/order'
import areaList from '../../../static/js/area.js'
// import { constants } from 'crypto'
export default {
  data () {
    return {
      isDownLoading: false, // 
      isUpLoading: false, // 
      upFinished: false, // 
      offset: 100, //  offset load
      areaList,
      active: 0,
      order_status: '0',
      list: [], // 
      getlist: {
        pageSize: 10, // 
        page: 0 // 
      }
    }
  },
  created () {
    this.active = this.$route.params.id
    // order_status
    this.order_status = this.$route.params.id
    // this.getListHandle()
    this.getListHandle()
  },
  methods: {
    // getCityFunc (content, key, jsonData) {
    //   for (var item in jsonData) {
    //     if (item == content) {
    //       key = jsonData[item]
    //     }
    //   }
    //   console.log(key)
    //   return key
    // },
    onClickLeft () {
      this.$router.go(-1)
    },
    // 
    async getListHandle () {
      let data = {
        order_status: this.order_status,
        page: this.getlist.page,
        pageSize: this.getlist.pageSize
      }
      await getListHandle(data).then(res => {
        console.log(res, '11111111')
        let data = res.data.data.data
        console.log(data, '')
        this.list = data
        let productListData = res.data.data.data
        if (res.data.code === '1') { // 
          if (this.list.length) { //  n
            if (this.isUpLoading) { // 
              this.list = this.list.concat(productListData) // 
              this.$nextTick(() => { //  DOM 
                this.isUpLoading = false// 
              })
              if (productListData.length < 10) { // 
                this.upFinished = true // 
              }
            }
            if (this.isDownLoading) { // 
              this.isDownLoading = false // 
              this.list = productListData // 
              if (this.upFinished) { // truefalse
                this.upFinished = false
              }
            }
          } else { //  
            this.list = productListData
          }
        }
        // 
        this.list.forEach(function (value) {
          // console.log(value.order_status)
          // 
          if (value.order_status == 1) {
            value.order_status = ''
          }
          if (value.order_status == 2) {
            value.order_status = ''
          }
          if (value.order_status == 3) {
            value.order_status = ''
          }
          if (value.order_status == 4) {
            value.order_status = ''
          }
          if (value.order_status == 5) {
            value.order_status = ''
          }
          if (value.order_status == 6) {
            value.order_status = ''
          }
        })
      })
    },
    // 
    onDownRefresh () {
      this.getlist.page = 1
      this.isDownLoading = true
      this.getListHandle()
      console.log('111')
    },
    // 
    onLoadList () {
      this.getlist.page++
      this.isUpLoading = true
      console.log('222')
      this.getListHandle()// ajax
    },
    onClick (name, title) {
      console.log(title)
      if (title == '') {
        console.log('pppp')
        this.order_status = '0'
        this.getListHandle()
      }
      if (title == '') {
        console.log('pppp')
        this.order_status = '1'
        this.getListHandle()
      }
      if (title == '') {
        console.log('pppp')
        this.order_status = '2'
        this.getListHandle()
      }
      if (title == '') {
        console.log('pppp')
        this.order_status = '3'
        this.getListHandle()
      }
      if (title == '') {
        console.log('pppp')
        this.order_status = '4'
        this.getListHandle()
      }
      if (title == '') {
        console.log('pppp')
        this.order_status = '6'
        this.getListHandle()
      }
    },
    // 
    clickList (index) {
      console.log(this.list[index].sn, '11111111')
      console.log(index)
      let id = this.list[index].sn
      this.$router.push({
        path: `/OrderDetail/${id}`
      })
    },
    // 
    clickList1 (index) {
      console.log(this.list[index].sn)
      console.log(index, '11111111')
      let id = this.list[index].sn
      this.$router.push({
