<template>
  <div>
    <van-nav-bar
      title=""
      left-text=""
      left-arrow
      @click-left="onClickLeft"
    />
    <div v-if="list.length == 0" class="addaddress">
      <div type="primary" @click="showPopup">
        <div class="add"><van-icon name="plus" /></div>
        <div></div>
        <!--   -->
        <!-- <van-popup v-model="show" position="bottom" :style="{ height: '100%' }">
          <van-nav-bar
            title=""
            left-text=""
            left-arrow
            @click-left="onClick"
          />
          <van-address-edit
            :area-list="areaList"
            :columns-num="3"
            show-delete
            show-set-default
            show-search-result
            :search-result="searchResult"
            @save="onSave"
            @delete="onDelete"
            @change-detail="onChangeDetail"
          />
        </van-popup> -->
        <!--   -->
      </div>
      <div><van-icon name="arrow" /></div>
    </div>
    <div v-else class="default">
      <div v-for="(item, index) in defaultAddress" :key="index">
        <div>
          <span>{{ item.name }}</span
          ><span>{{ item.tel }}</span>
        </div>
        <div>{{ item.address }}</div>
      </div>
      <div @click="showPopup"> ></div>
    </div>
    <!--  -->
    <van-popup v-model="show" position="bottom" :style="{ height: '70%' }">
      <van-nav-bar
        title=""
        left-text=""
        left-arrow
        @click-left="back"
      />
      <!--  -->
      <van-address-list
        v-model="chosenAddressId"
        :list="list"
        @edit="onEdit"
        @select="onSelect"
      />
    </van-popup>

    <!--  -->
    <div class="product_list">
      <div class="list" v-for="(item, index) in goods_list" :key="index">
        <div>
          <img
            :src="'https://worldview.com.cn/uploads/' + item.thumb_img"
            alt=""
          />
        </div>
        <div class="product">
          <div>{{ item.name }}</div>
          <div>{{ item.specification }}</div>
          <div class="price">
            <div>{{ item.price }}</div>
            <div>X{{ item.num }}</div>
          </div>
        </div>
      </div>
    </div>
    <van-submit-bar
      :price="totalPrice"
      button-text=""
      @submit="onSubmit"
    />
  </div>
</template>

<script>
import { getInfoHandle, confirmHandle } from '@/api/order'
import { addressHandle, editaddressHandle, deladdressHandle, getInfoaddressHandle } from '@/api/address'
import areaList from '../../../static/js/area.js'
import { filter } from 'minimatch'

export default {
  data () {
    return {
      areaList,
      chosenAddressId: '',
      list: [{}],
      show: false, // 
      totalPrice: 0, // 
      goods_list: '', // 
      defaultAddress: [], // 
      firstAddress: [], // 
      order_id: ''
    }
  },
  created () {
    this.getInfoHandle()
    this.addressHandle()
  },
  // computed: {
  //   totalPrice: {
  //     get: function () {
  //       return this.list.reduce((total, item) => total + (this.checkedGoods.indexOf(item) !== -1 ? item.bulk_price * 100 * item.num : 0)
  //         , 0)
  //     }

  //   }
  // },
  methods: {
    getCityFunc (content, key, jsonData) {
      for (var item in jsonData) {
        if (item == content) {
          key = jsonData[item]
        }
      }
      // console.log(key)
      return key
    },
    // 
    showPopup () {
      this.show = true
    },
    onClickLeft () {
      this.$router.go(-1)
    },
    // 
    // onClick () {
    //   this.show = false
    //   console.log(0)
    // },
    // 
    back () {
      this.show = false
      // console.log(11)
    },
    // 
    addressHandle () {
      let _this = this
      addressHandle().then(res => {
        let { data } = { ...res.data }
        console.log(data)
        // for (var index in data) {
        //   // console.log(index)
        //   console.log(data[index].province)
        //   // this.getCityFunc(data[index].province, this.province, this.areaList.province_list)
        // }
        var newArr = data.map(function (v, i) {
          // console.log(v.province + '')
          // console.log(v)
          return {
            id: v.id, // 
            name: v.consignee, // 
            tel: v.mobile, // 
            address: _this.getCityFunc(v.province, _this.province, _this.areaList.province_list) + _this.getCityFunc(v.city, _this.city, _this.areaList.city_list) + _this.getCityFunc(v.area, _this.area, _this.areaList.county_list) + v.detail_address, // 
            area: v.area, // 
            city: v.city,
            county: v.province,
            isDefault: v.is_default// 
          }
        })
        this.list = newArr
        console.log(this.list, 'sss')
        //  1 
        console.log(this.list.filter(item => item.isDefault == 1))
        this.defaultAddress = this.list.filter(item => item.isDefault == 1)
        // 
        console.log(this.list)
        this.firstAddress = this.list.slice(1)
        console.log(this.firstAddress, '')
        console.log(this.list.slice(1), '2222222222222')
        //   
        if (this.defaultAddress == '') {
          this.defaultAddress = this.firstAddress
        }
      })
    },
    // 
    onEdit () {
      // console.log('')
    },
    //   
    addAddress () {
      console.log(0)
    },
    // 
    onSelect (item) {
      console.log([item])
      this.defaultAddress = [item]
      this.show = false
    },
    // 
    popup () {
      this.show = true
    },
    // 
    onSubmit () {
      console.log(this.$route.params.id)
      let orderParams = {
        order_id: this.order_id,
        consignee: this.defaultAddress[0].name,
        mobile: this.defaultAddress[0].tel,
        province: this.defaultAddress[0].county,
        city: this.defaultAddress[0].city,
        area: this.defaultAddress[0].area,
        detail_address: this.defaultAddress[0].address

      }
      confirmHandle(orderParams).then(res => {
        console.log(res)
        console.log(this.defaultAddress)
        let id = this.$route.params.id
        this.$router.push({
          path: `/OrderDetail/${id}`
        })
      })
    },
    // 
    getInfoHandle () {
      var id = {
        id: this.$route.params.id
      }
      // console.log(id.id)
      getInfoHandle(id.id).then(res => {
        console.log(res, '')
        console.log(res.data.data.goods_list)
        console.log(res.data.data.id)
        this.order_id = res.data.data.id
        this.goods_list = res.data.data.goods_list
        console.log(this.goods_list, 'sssssssssss')
        // 
        this.totalPrice = this.goods_list.reduce((total, item) => total + item.price * item.num * 100, 0)
        // console.log(typeof (parseInt(this.goods_list[0].price)))

        console.log(res.data.data.goods_list)
      })
    }
  }
}
</script>

<style lang="less" scoped>
  .default {
    display: flex;
    justify-content: space-between;
    padding: 10px 16px;
    border-bottom: 1px solid #ccc;
    align-items: center;
  }
  .addaddress {
    border-top: 3px solid #efefef;
    border-bottom: 3px solid #efefef;
    height: 40px;
    padding: 0 10px;
    align-items: center;
    display: flex;
    justify-content: space-between;
    > div:nth-of-type(1) {
      display: flex;
    }
    .add {
      width: 16px;
      height: 16px;
      line-height: 16px;
      background: red;
      text-align: center;
      color: #fff;
      margin-right: 4px;
    }
  }
  .product_list {
    margin-bottom: 100px;
  }
  .list {
    display: flex;
    padding: 14px;
    border-bottom: 1px solid #ddd;
    > div:nth-of-type(1) {
      width: 100px;
      img {
        width: 100%;
      }
    }
    > div:nth-of-type(2) {
      flex: 1;
    }
    .product {
      margin-left: 8px;
      .price {
        display: flex;
        justify-content: space-between;
        margin-top: 24px;
        > div:nth-of-type(1) {
          color: red;
        }
      }
      > div:nth-of-type(2) {
        color: #999;
        margin: 2px 0;
      }
    }
  }
  .van-submit-bar {
    border-top: 1px solid #ccc;
  }
</style>
